/*
 * The bordered fields are based on / inspired by this CodePen: http://codepen.io/prajwal078/pen/LVJZXz?editors=010
 */

// Use of this module requires the user to include variables from material-design-lite
//@import "../../node_modules/material-design-lite/src/variables";
//@import "../../node_modules/material-design-lite/src/mixins";

@import "../variables";


/* ==========  Bordered fields  ========== */
$mdlext-bordered-field-border-width                   : 1px !default;
$mdlext-bordered-field-border-radius                  : 3px !default;
$mdlext-bordered-field-height                         : 40px !default;
$mdlext-bordered-field-padding                        : $input-text-padding + 4px !default;
$mdlext-bordered-field-padding-top                    : 0 !default;
$mdlext-bordered-field-padding-bottom                 : 0 !default;

$mdlext-bordered-field-background-color               : rgba(#FFFFFF, 0.1) !default;
$mdlext-bordered-field-focus-border-color             : unquote("rgb(#{$color-primary})") !default;
$mdlext-bordered-field-focus-background-color         : $mdlext-bordered-field-background-color !default;

$mdlext-bordered-field-disabled-border-color          : $input-text-disabled-color !default;
$mdlext-bordered-field-disabled-background-color      : $mdlext-bordered-field-background-color !default;

$mdlext-bordered-field-required-border-color          : unquote("rgba(#{$color-accent}, 0.8)") !default;
$mdlext-bordered-field-required-background-color      : $mdlext-bordered-field-background-color !default;

$mdlext-bordered-field-label-top                      : 45px !default;
$mdlext-bordered-field-label-focused-top              : 20px !default;

// New
$mdlext-bordered-field-vertical-spacing-top           : $input-text-vertical-spacing !default;
$mdlext-bordered-field-vertical-spacing-bottom        : $input-text-vertical-spacing !default;
$mdlext-bordered-field-font-size                      : $input-text-font-size !default;
$mdlext-bordered-field-font-weight                    : normal !default;

$mdlext-bordered-field-border-color                   : $input-text-bottom-border-color !default;

$mdlext-bordered-field-required-focus-border-color    : unquote("rgba(#{$color-accent}, 0.8)") !default;
$mdlext-bordered-field-required-focus-background-color: $mdlext-bordered-field-background-color !default;

$mdlext-bordered-field-error-border-color             : $input-text-error-color;
$mdlext-bordered-field-error-background-color         : $mdlext-bordered-field-background-color;
$mdlext-bordered-field-error-focus-border-color       : $input-text-error-color;
$mdlext-bordered-field-error-focus-background-color   : $mdlext-bordered-field-error-background-color;


// "Theme" for this styling
.mdlext-bordered-fields {

  box-sizing: border-box;

  // Styling for textfield and selectfield.
  .mdl-textfield,
  .mdlext-selectfield {
    box-sizing: border-box;
    padding: $mdlext-bordered-field-vertical-spacing-top 0 $mdlext-bordered-field-vertical-spacing-bottom 0;

    .mdl-textfield__input,
    .mdlext-selectfield__select {
      box-sizing: border-box;
      height: $mdlext-bordered-field-height;
      background-color: $mdlext-bordered-field-background-color;
      border: $mdlext-bordered-field-border-width solid $mdlext-bordered-field-border-color;
      border-radius: $mdlext-bordered-field-border-radius;
      padding-top: $mdlext-bordered-field-padding-top;
      padding-right: $mdlext-bordered-field-padding;
      padding-bottom: $mdlext-bordered-field-padding-bottom;
      padding-left: $mdlext-bordered-field-padding;
      font-size: $mdlext-bordered-field-font-size;
      font-weight: $mdlext-bordered-field-font-weight;

      &:disabled {
        color: $input-text-disabled-text-color;
        background-color: $mdlext-bordered-field-disabled-background-color;
        border-color: $mdlext-bordered-field-disabled-border-color;
      }
      &:required {
        background-color: $mdlext-bordered-field-required-background-color;
        border-color: $mdlext-bordered-field-required-border-color;
      }
      &:focus,
      &:required:focus {
        background-color: $mdlext-bordered-field-focus-background-color;
        border-color: $mdlext-bordered-field-focus-border-color;
      }
    }

    .mdlext-selectfield__select {
      padding-right: calc(1em + #{$input-text-padding});  // space for down arrow
    }

    &.is-dirty {
      .mdl-textfield__input,
      .mdlext-selectfield__select {
        &:required {
          border-color: $mdlext-bordered-field-border-color;
          background-color: $mdlext-bordered-field-required-background-color;
        }
        &:focus,
        &:required:focus {
          border-color: $mdlext-bordered-field-focus-border-color;
          background-color: $mdlext-bordered-field-focus-background-color;
        }
      }
    }
  }

  .mdlext-selectfield:after {
    top: calc(50% - #{($mdlext-bordered-field-vertical-spacing-top - $mdlext-bordered-field-vertical-spacing-bottom)/2});
    //transform: translateY(-50%);
  }

  // Styling for the label / floating label.
  .mdl-textfield,
  .mdlext-selectfield {

    &.is-focused {
      .mdl-textfield__label,
      .mdlext-selectfield__label {
        top: $mdlext-bordered-field-label-focused-top;
      }
    }
    &.is-dirty {
      .mdl-textfield__label,
      .mdlext-selectfield__label {
        top: $mdlext-bordered-field-label-focused-top;
        visibility: visible;
      }
    }
    .mdl-textfield__label,
    .mdlext-selectfield__label {
      padding-left: $mdlext-bordered-field-padding;

      //top: $mdlext-bordered-field-label-top;
      top: calc(50% - #{($mdlext-bordered-field-vertical-spacing-top - $mdlext-bordered-field-vertical-spacing-top)/2});
      bottom: auto;
      transform: translateY(-50%);

      // Hides the colored underline for the TextField.
      &:after {
        background-color: transparent !important;
        visibility: hidden !important;
      }
    }
    &.has-placeholder,
    &.has-placeholder {
      .mdl-textfield__label,
      .mdlext-selectfield__label {
        top: $mdlext-bordered-field-label-focused-top;
      }
    }
  }

  // Icon(s) and/or button(s) inside textfield
  .mdl-textfield,
  .mdlext-selectfield {
    &.mdlext-bordered-fields__icon-left,
    &.mdlext-bordered-fields__icon-right {
      & > i,
      & > .mdl-button {
        position: absolute;
        top: calc(50% - #{($mdlext-bordered-field-vertical-spacing-top - $mdlext-bordered-field-vertical-spacing-bottom)/2});
        transform: translateY(-50%);
      }
    }
    &.mdlext-bordered-fields__icon-left {
      & > i:first-child,
      & > .mdl-button:first-child {
        left: $input-text-padding;
      }
      & > i ~ .mdl-textfield__input,
      & > .mdl-button ~ .mdl-textfield__input,
      & > i ~ .mdlext-selectfield__select,
      & > .mdl-button ~ .mdlext-selectfield__select {
        padding-left: $input-text-button-size;
      }
      & > i ~ .mdl-textfield__label,
      & > .mdl-button ~ .mdl-textfield__label {
        left: $input-text-button-size - $input-text-padding;
      }
      & > i ~ .mdlext-selectfield__label,
      & > .mdl-button ~ .mdlext-selectfield__label {
        left: $input-text-button-size - $mdlext-bordered-field-padding;
      }

    }
    &.mdlext-bordered-fields__icon-right {
      & > .mdl-textfield__input {
        padding-right: $input-text-button-size;
      }
      & > i:last-child,
      & > .mdl-button:last-child {
        left: auto;
        right: $input-text-padding;
      }
    }
    &.is-disabled i,
    &.is-disabled .mdl-button {
      color: $input-text-disabled-color;
    }
  }
}




